  position: relative;
  z-index: 10;
} */

@media only screen and (min-width: 750px) and (max-width: 1000px) {
  /* Css to applied when media query is fired */
  .container {
    max-width: 70%;
  }
  .not-imp {
    padding-top: 15px;
    padding-left: 10px;
  }
  .navbar-expand-lg .navbar-collapse {
    display: flex !important;
    flex-basis: auto;
  }
  .navbar-nav {
    flex-direction: row;
  }
  /* .form-control{
      width: 90%;
  } */
}
@media only screen and (min-width: 500px) and (max-width: 750px) {
  /* Css to applied when media query is fired */
  .container {
    max-width: 80%;
  }
  .not-imp {
    padding-top: 15px;
    padding-left: 10px;
  }
  .navbar-expand-lg .navbar-collapse {
    display: flex !important;
    flex-basis: auto;
  }
  .navbar-nav {
    flex-direction: row;
  }
  /* .form-control{
      width: 90%;
  } */
}
@media only screen and (min-width: 0px) and (max-width: 500px) {
  /* Css to applied when media query is fired */
  .container {
    max-width: 90%;
  }
  .container h1 {
    font-size: 1.2rem;
  }
  .not-imp {
    display: none;
  }
  .navbar-expand-lg .navbar-collapse {
    display: flex !important;
    flex-basis: auto;
  }
  .navbar-nav {
    flex-direction: row;
  }
  /* .form-control{
      width: 90%;
  } */
}
